@keyframes flip-animation {
  0% {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(90deg);
  }
  51% {
    transform: rotateY(270deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
:host {
  /* width */
  /* Track */
  /* Handle */
  /* Handle on hover */
  display: flex;
  flex-direction: column;
  perspective: 1000px;
}
:host *,
:host *::after,
:host *::before {
  box-sizing: border-box;
}
:host ::-webkit-scrollbar-button {
  display: none;
}
@-moz-document url-prefix() {
  :host * {
    scrollbar-color: var(--theme-scrollbar-thumb--background) var(--theme-scrollbar-track--background);
    scrollbar-width: thin;
  }
}
:host ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
:host ::-webkit-scrollbar-track {
  border-radius: 5px;
  background: var(--theme-scrollbar-track--background);
}
:host ::-webkit-scrollbar-track:hover {
  background: var(--theme-scrollbar-track--background--hover);
}
:host ::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: var(--theme-scrollbar-thumb--background);
}
:host ::-webkit-scrollbar-thumb:hover {
  background: var(--theme-scrollbar-thumb--background--hover);
}
:host ::-webkit-scrollbar-corner {
  display: none;
}
:host .flip-tile-header {
  display: flex;
  align-items: center;
  height: 2.5rem;
  padding: 0 0.5rem 0 1rem;
}
:host .flip-tile-header .header-slot-container {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-grow: 1;
  min-width: 0;
}
:host .content-container {
  flex-grow: 1;
  margin: 1rem;
}
:host .flip-tile-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--theme-blind-base--background);
  border: solid 1px var(--theme-blind-base--border-color);
  border-radius: var(--theme-flip-tile--border-radius) var(--theme-flip-tile--border-radius) 0 0;
  transform-style: preserve-3d;
}
:host .flip-tile-container.flip-animation-active {
  animation: flip-animation 300ms, ease-in-out;
}
:host .flip-tile-container .footer {
  display: flex;
  height: 3rem;
  align-items: center;
  justify-content: center;
  padding: 0 0.5rem;
  color: var(--theme-flip-footer--color);
  background-color: var(--theme-blind-base--background);
}
:host .flip-tile-container .footer :first-child {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 100%;
}
:host .flip-tile-container ::slotted(*) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;
}
:host .flip-tile-container.primary {
  border-color: var(--theme-color-primary);
}
:host .flip-tile-container.primary .footer {
  background-color: var(--theme-color-primary);
  color: var(--theme-color-primary--contrast);
}
:host .flip-tile-container.info {
  border-color: var(--theme-color-info);
}
:host .flip-tile-container.info .footer {
  background-color: var(--theme-color-info);
  color: var(--theme-color-info--contrast);
}
:host .flip-tile-container.warning {
  border-color: var(--theme-color-warning);
}
:host .flip-tile-container.warning .footer {
  background-color: var(--theme-color-warning);
  color: var(--theme-color-warning--contrast);
}
:host .flip-tile-container.alarm {
  border-color: var(--theme-color-alarm);
}
:host .flip-tile-container.alarm .footer {
  background-color: var(--theme-color-alarm);
  color: var(--theme-color-alarm--contrast);
}
:host:hover .flip-tile-container .footer ix-icon {
  color: var(--theme-color-std-text);
}